import React from 'react';
import Lottie from 'lottie-react';
import julieta from '../assets/julieta.jpeg';
import compu from '../assets/compu con codigo.json';
import '../styles/custom.css';

export default function AboutPage() {
  return (
    <section className="about-section with-lottie">
      <div className="background-lottie">
        <Lottie animationData={compu} loop autoplay className="lottie-bg" />
      </div>

      <div className="about-container">
        <div className="about-image">
          <img src={julieta} alt="Julieta Meza" className="profile-img" />
        </div>

        <div className="about-content">
          <h2 className="fade-in-top">Sobre m</h2>
          <h3 className="fade-in-top delay-1">Julieta Meza</h3>
          <p className="subtitle fade-in-top delay-2"></p>
          <p className="fade-in-top delay-3">
            Programadora universitaria de 22 aos,recibida en UTN FR Tucumn. Combino desarrollo <strong>backend</strong> y <strong>frontend</strong> para crear soluciones digitales completas, modernas y responsive.
          </p>
          <p className="fade-in-top delay-4">
            Desarrollo sistemas a medida, sitios web, landing pages, y aprovecho las ventajas de las Progressive Web Apps para lograr velocidad, funcionalidad y usabilidad.
          </p>
          <div className="about-work fade-in-top delay-5">
            <h4>Cmo trabajo?</h4>
            <ul>
              <li> Entregas en tiempo y forma con enfoque al detalle.</li>
              <li> Comunicacin clara y constante con el cliente.</li>
              <li> Me adapto, aprendo rpido y colaboro en equipo o en forma autnoma.</li>
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}
